import {createRouter, createWebHashHistory} from 'vue-router';

const routes = [
	{
		path: '/',
		component: () => import('./../pages/index/index.vue')
	},
	{
		path: '/menu',
		component: () => import('./../pages/menu/index.vue')
	},
	{
		path: '/archive',
		component: () => import('./../pages/archive/index.vue')
	},
	{
		path: '/events-statistics/wj',
		component: () => import('./../pages/events-statistics/wj.vue')
	},
	{
		path: '/events-statistics/pf',
		component: () => import('./../pages/events-statistics/pf.vue')
	},
	{
		path: "/search",
		component: () => import('./../pages/search/index.vue')
	},
	{
		path: "/diary",
		component: () => import('./../pages/diary/index.vue'),
	},
	{
		path: "/diary/detail",
		component: () => import('./../pages/diary-detail/index.vue')
	},
	{
		path: "/account",
		component: () => import('./../pages/account/index.vue')
	},
	{
		path: "/account/detail",
		component: () => import('./../pages/account-detail/index.vue')
	},
	{
		path: "/landlord",
		component: () => import('./../pages/landlord/index.vue')
	},
	{
		path: "/:pathMatch(.*)",
		component: () => import('./../pages/404/index.vue')
	}
];

const router = createRouter({
	history: createWebHashHistory(),
	routes
})

export default router;